import React, { useEffect, useState } from 'react'
import { Container, Col, Form, FormGroup, Label, Input, Button, Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, NavbarText, Offcanvas, OffcanvasHeader, OffcanvasBody, InputGroup, InputGroupText, Row, Alert, Table, } from 'reactstrap';
import { TbUbicaciones } from '../../../CapaEntidades/Servivios/TbUbicaciones';
import { AbrirMapa, ActualizarCliente, buscarSuscriptor, buscarSuscriptor2, cantClientes, habilitarAgregar, habilitarEditar, habilitarSuscriptor, limpiarCampos, ListaCompleta, realizarLlamada, RegistarCliente } from '../../../CapaNegocios/NegociosServicios/NegociosUbicaciones';
import Logo from '../../Assets/logos/Logo-Ubicate.png';
import Logo2 from '../../Assets/logos/Logo2024-11.png';
import { Await } from 'react-router-dom';
import { WarningMessage } from '../../../CapaUtilidades/Utils/FuncionesDeReutilizacion';
const ServiciosLR = () => {

  const TBUbicaciones = TbUbicaciones();

  const [paginaActual, setPaginaActual] = useState(1);
  const [clientesPorPagina] = useState(18);

  const indiceUltimoCliente = paginaActual * clientesPorPagina;
  const indicePrimerCliente = indiceUltimoCliente - clientesPorPagina;

  // Obtener los clientes a mostrar en la página actual
  const clientesAmostrar = TBUbicaciones.datos.slice(indicePrimerCliente, indiceUltimoCliente);

  // Función para cambiar de página
  const cambiarPagina = (numeroPagina) => {
    setPaginaActual(numeroPagina);
  };


  const handleKeyPress = (event) => {

    buscarSuscriptor(TBUbicaciones);

  };

  const verTodosLosDatos = async () => {
    if (!navigator.onLine) {
      TBUbicaciones.setLoading(false); // Detener cualquier indicador de carga
      WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
      return;
    }
    await ListaCompleta(TBUbicaciones);
    //alert('Datos después de ListaCompleta:', TBUbicaciones.datos); // Verifica si los datos existen aquí

  };

  const CargarDatos = async (suscriptor) => {

    TBUbicaciones.setBuscarSuscriptor(suscriptor);
    buscarSuscriptor2(TBUbicaciones, suscriptor);
    TBUbicaciones.setMostrarListado(false);
  }

  useEffect(() => {
    if (!navigator.onLine) {
      TBUbicaciones.setLoading(false); // Detener cualquier indicador de carga
      WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
      return;
    }
    //console.log('Datos actualizados:', TBUbicaciones.datos);
  }, [TBUbicaciones.datos]);

  useEffect(() => {
    cantClientes(TBUbicaciones)
    //console.log('Datos actualizados:', TBUbicaciones.datos);
  }, [TBUbicaciones.canti]);


  return (
    <Container fluid className='    p-0'>
      <Container fluid className='headerApp p-2'>
        {/* <Row className='headerApp py-2'>
        {TBUbicaciones.mostrarListado &&
          <Col className='   text-center'>
            <img
              alt="logo"
              src={Logo}
              className="logo  "
            />
            <img
              alt="logo"
              src={Logo2}
              className="logo2  "
            />

          </Col>
        }
        </Row> */}
        <Col className='d-flex flex-wrap justify-content-between  headerApp contBody p-0 m-0  '>
          {!TBUbicaciones.mostrarListado &&
            <>
              <Col className=" ">
                <Label className='mt-2 text-center text-white '>Clientes Registrados {TBUbicaciones.canti}</Label>

              </Col >
              <Col className="d-flex justify-content-end mb-1">
                <Button className="btnVerDatos d-flex" onClick={(e) => verTodosLosDatos()}>
                  Ver Lista
                </Button>
              </Col >
            </>
          }
        </Col>
      </Container>

      <Container fluid className='px-2 m-0  py-0'>
        {TBUbicaciones.mostrarListado ? (
          <>
          <Col className="m-0 text-center">
            <Label className='mt-2 text-center'>
              Clientes Registrados {TBUbicaciones.canti}
            </Label>
          </Col>
          <Container className="shadow-lg mt-0 mb-1 p-0 containerTable br" style={{ overflowX: 'auto' }}>
            <Container className='br pt-0 px-0 '>
              <Table className="shadow2-2 border table table-striped table-bordered w-100 shadow br mt-0 pt-0 br" style={{ minWidth: '300px', borderRadius: '15px' }}>
                <thead className="border-bottom">
                  <tr>
                    <th className='text-center'>Suscriptor</th>
                    <th className='text-center'>Cliente</th>
                  </tr>
                </thead>
                <tbody className="tablebody" id="tablebody">
                  {clientesAmostrar.length > 0 ? (
                    clientesAmostrar.map((dato, index) => (
                      <tr key={index}>
                        <td className='text-center'>
                          <a
                            className='link'
                            href="#"
                            onClick={(e) => {
                              e.preventDefault(); // Evita el comportamiento predeterminado del enlace
                              CargarDatos(dato.suscriptor); // Llama a la función manejadora
                            }}
                          >
                            {dato.suscriptor}
                          </a>
                        </td>
                        <td>{dato.nombreCompleto}</td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="2" style={{ textAlign: 'center' }}>
                        No hay datos disponibles.
                      </td>
                    </tr>
                  )}
                </tbody>
              </Table>
            </Container>
          </Container>
    
          <Col className="d-flex justify-content-center mt-2 mb-2">
            <Button
              onClick={() => cambiarPagina(paginaActual - 1)}
              disabled={paginaActual === 1}
            >
              Anterior
            </Button>
            <Label className="mx-2">
              Página {paginaActual} de {Math.ceil(TBUbicaciones.canti / clientesPorPagina)}
            </Label>
            <Button
              onClick={() => cambiarPagina(paginaActual + 1)}
              disabled={paginaActual === Math.ceil(TBUbicaciones.canti / clientesPorPagina)}
            >
              Siguiente
            </Button>
          </Col>
    
          <Button className='btnVolver' onClick={(e) => TBUbicaciones.setMostrarListado(false)}>
            Volver
          </Button>
        </>

        ) : (
          <>
            <Container className='shadow-lg mt-2 mb-3 br p-2'>

              <Row>

                <FormGroup>
                  <Label for="password" className="font-size-login">
                    Ingrese el número de suscriptor
                  </Label>
                  <InputGroup>
                    <Input
                      required
                      id="password"
                      name="password"
                      type='number'
                      value={TBUbicaciones.buscarSuscriptor}
                      onChange={(e) => TBUbicaciones.setBuscarSuscriptor(e.target.value)}
                      onFocus={() => limpiarCampos(TBUbicaciones)}
                      onKeyDown={(e) => {
                        if (e.key === "Enter") {
                          e.target.blur();
                          handleKeyPress(e.key);
                        }
                      }}
                    />
                    <InputGroupText addonType="append" className='p-0 Btnsearch'>
                      <Button onClick={() => buscarSuscriptor(TBUbicaciones)} color="" className='fabText p-0 px-2'>
                        🔍
                      </Button>
                    </InputGroupText>
                  </InputGroup>
                </FormGroup>
                <Col className='d-flex justify-content-between align-items-center py-2'>
                  <Button
                    className={`btnNuevo ${TBUbicaciones.mostrarAgregar ? 'btn-disabled' : ''}`}
                    disabled={TBUbicaciones.mostrarAgregar}
                    onClick={(e) => habilitarAgregar(TBUbicaciones)}>➕</Button>
                  <Button
                    className={`btnEditar ${TBUbicaciones.mostrarEditar ? 'btn-disabled' : ''}`}
                    disabled={TBUbicaciones.mostrarEditar}
                    onClick={(e) => habilitarEditar(TBUbicaciones)}>✏️</Button>
                  <Button className='btnLimpiar' onClick={(e) => limpiarCampos(TBUbicaciones)}>🧹</Button>

                </Col>
              </Row>
            </Container>


            <Container className='shadow-lg   mt-2 mb-3 br p-1 '>

              {TBUbicaciones.loading && (
                <Col className="loading-overlay">
                  <Col className="loading-content d">
                    <Col className="loading-spinner"></Col>
                    <div class="loading-text">
                      <span>C </span>
                      <span>a </span>
                      <span>r </span>
                      <span>g </span>
                      <span>a </span>
                      <span>n </span>
                      <span>d </span>
                      <span>o </span>

                    </div>
                  </Col>
                </Col>
              )}
              <Container className="">
                <h2 className="title2">Datos del Suscriptor</h2>
                <p className="note required">* Campos obligatorios</p>

                {/* Suscriptor */}
                <Col className="field-container p-0">
                  <Label className="field-label">Número de Suscriptor <span className="required">*</span></Label>
                  <Col className="input-container">
                    <Input
                      disabled={TBUbicaciones.modificarSuscritor}
                      type="number"
                      value={TBUbicaciones.suscriptor}
                      onChange={e => TBUbicaciones.setSuscriptor(e.target.value)}
                      className={`input ${TBUbicaciones.modificarSuscritor ? 'input-disabled' : ''}`}
                    />
                    <Button
                      disabled={TBUbicaciones.mostrarEditarSuscrip}
                      className={`btnEditar2 ${TBUbicaciones.mostrarEditarSuscrip ? 'btn-disabled' : ''}`}
                      onClick={() => habilitarSuscriptor(TBUbicaciones)}>
                      ✏️
                    </Button>
                  </Col>
                </Col>
                {/* Cliente */}
                <Col className="field-container">
                  <Label className="field-label">Nombre Completo <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.nombreCompleto}
                    onChange={e => TBUbicaciones.setNombreCompleto(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>
                {/* Teléfonos 1 */}
                <Col className="field-container"  >
                  <Label className="field-label">Teléfono 1</Label>
                  <Col className="input-container">
                    <Input
                      disabled={TBUbicaciones.editar}
                      type="tel"
                      value={TBUbicaciones.telefono1}
                      onChange={e => TBUbicaciones.setTelefono1(e.target.value)}
                      className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                    />
                    <Button
                      disabled={TBUbicaciones.habilitarLlamadas}
                      className={`Btnsearch ${TBUbicaciones.habilitarLlamadas ? 'btn-disabled' : ''}`}
                      onClick={() => realizarLlamada(TBUbicaciones.telefono1)}>
                      📞
                    </Button>
                  </Col>
                </Col>
                {/* Teléfonos 2 */}
                <Col className="field-container"  >
                  <Label className="field-label">Teléfono 2</Label>
                  <Col className="input-container">
                    <Input
                      disabled={TBUbicaciones.editar}
                      type="tel"
                      value={TBUbicaciones.telefono2}
                      onChange={e => TBUbicaciones.setTelefono2(e.target.value)}
                      className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                    />
                    <Button
                      disabled={TBUbicaciones.habilitarLlamadas}
                      className={`Btnsearch ${TBUbicaciones.habilitarLlamadas ? 'btn-disabled' : ''}`}
                      onClick={() => realizarLlamada(TBUbicaciones.telefono2)}>
                      📞
                    </Button>
                  </Col>
                </Col>
                {/* Teléfonos 2 */}
                <Col className="field-container"  >
                  <Label className="field-label">Teléfono 3</Label>
                  <Col className="input-container">
                    <Input
                      disabled={TBUbicaciones.editar}
                      type="tel"
                      value={TBUbicaciones.telefono3}
                      onChange={e => TBUbicaciones.setTelefono3(e.target.value)}
                      className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                    />
                    <Button
                      disabled={TBUbicaciones.habilitarLlamadas}
                      className={`Btnsearch ${TBUbicaciones.habilitarLlamadas ? 'btn-disabled' : ''}`}
                      onClick={() => realizarLlamada(TBUbicaciones.telefono3)}>
                      📞
                    </Button>
                  </Col>
                </Col>

                {/* PLAN */}
                <Col className="field-container">
                  <Label className="field-label">Plan Contratado <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.plan}
                    onChange={e => TBUbicaciones.setPlan(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* NODO */}
                <Col className="field-container">
                  <Label className="field-label">Nodo  </Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.nodo}
                    onChange={e => TBUbicaciones.setNodo(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* COLILLA */}
                <Col className="field-container">
                  <Label className="field-label">Colilla</Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="number"
                    value={TBUbicaciones.colilla}
                    onChange={e => TBUbicaciones.setColilla(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* TAP */}
                <Col className="field-container">
                  <Label className="field-label">TAP</Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="number"
                    value={TBUbicaciones.tap}
                    onChange={e => TBUbicaciones.setTap(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* NAP */}
                <Col className="field-container">
                  <Label className="field-label">NAP</Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="Number"
                    value={TBUbicaciones.nap}
                    onChange={e => TBUbicaciones.setNap(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* MAC */}
                <Col className="field-container">
                  <Label className="field-label">MAC <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.mac}
                    onChange={e => TBUbicaciones.setMac(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* SN */}
                <Col className="field-container">
                  <Label className="field-label">SN <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.sn}
                    onChange={e => TBUbicaciones.setSn(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* IP */}
                <Col className="field-container">
                  <Label className="field-label">IP <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.ip}
                    onChange={e => TBUbicaciones.setIP(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* Direccion */}
                <Col className="field-container">
                  <Label className="field-label">Dirección <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="textarea"
                    multiple="true"
                    rows="5"
                    value={TBUbicaciones.direccion}
                    onChange={e => TBUbicaciones.setDireccion(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>

                {/* Ubicacion */}
                <Col className="field-container">
                  <Label className="field-label">Ubicación <span className="required">*</span></Label>
                  <Input
                    disabled={TBUbicaciones.editar}
                    type="text"
                    value={TBUbicaciones.ubicacion}
                    onChange={e => TBUbicaciones.setUbicacion(e.target.value)}
                    className={`input ${TBUbicaciones.editar ? 'input-disabled' : ''}`}
                  />
                </Col>
                <Button
                  disabled={TBUbicaciones.mostrarGPS}
                  className={`buttonGps ${TBUbicaciones.mostrarGPS ? 'btn-disabled' : ''}`}
                  onClick={() => AbrirMapa(TBUbicaciones)}>
                  Abrir en Maps
                </Button>
              </Container>
              {/* Botones de guardar y eliminar */}
              <Col className=" d-flex justify-content-between align-items-center p-2">
                <Button
                  className={`btnGuardar ${TBUbicaciones.habilitarGuardar ? 'btn-disabled' : ''}`}
                  disabled={TBUbicaciones.habilitarGuardar}
                  onClick={() => {
                    if (TBUbicaciones.esNuevo) {
                      RegistarCliente(TBUbicaciones);
                    } else {
                      ActualizarCliente(TBUbicaciones);
                    }
                  }}>
                  💾
                </Button>
                <Button
                  className={`btnEliminar ${TBUbicaciones.habilitarEliminar ? 'btn-disabled' : ''}`}
                  disabled={TBUbicaciones.habilitarEliminar}
                  onClick={() => limpiarCampos(TBUbicaciones)}>
                  🗑
                </Button>
              </Col>

            </Container>
            <Container className='shadow-lg   mt-2 mb-3 br p-1 text-center headerApp'>
              <Label className='text-center mt-1'>
                Developed by J3 Tech Solutions © 2024
              </Label>
            </Container>
          </>

        )}

      </Container>
    </Container>
  )
}

export default ServiciosLR
