


import { TbUbicaciones } from "../../CapaEntidades/Servivios/TbUbicaciones";
import { actualizarSuscriptor, cantidadClientes, existeSuscriptor, guardarSuscriptor, obtenerCliente, obtenerlistaCompleta } from "../../CapaDatos/DatosServicios/DatosUbicaciones";
import { ErrorMessage, InfoMessage, SuccessMessage, WarningMessage } from "../../CapaUtilidades/Utils/FuncionesDeReutilizacion";
import Swal from "sweetalert2";


export const realizarLlamada = (telefono) => {
    if (!telefono || telefono.trim() === '') {

        return;
    }

    // Usar el protocolo 'tel:' para abrir la aplicación de llamadas
    window.location.href = `tel:${telefono}`;

};


/**
 * Método para buscar y obtener los datos del suscriptor en la base de datos.
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene el estado y las funciones necesarias para gestionar los datos y la interfaz de usuario.
 * 
 * Este método realiza lo siguiente:
 * 1. Valida que el campo de búsqueda de suscriptor no esté vacío.
 * 2. Realiza una solicitud a la API para obtener los datos del suscriptor si se encuentra registrado.
 * 3. Si el suscriptor no se encuentra, muestra una alerta y ofrece la opción de registrar uno nuevo.
 * 4. Si ocurre un error durante la búsqueda, muestra una alerta de error.
 * 
 * @returns {void} No retorna ningún valor explícito. Modifica el estado de `TBUbicaciones` según el resultado de la búsqueda.
 */
export const buscarSuscriptor = async (TBUbicaciones) => {
    try {

        TBUbicaciones.setMostrarEditar(true);
        TBUbicaciones.setMostarAgregar(true);

        // Verificar conexión a internet
        if (!navigator.onLine) {
            TBUbicaciones.setLoading(false);
            WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
            return;
        }
        // Verificar que el nombre del suscriptor no esté vacío
        if (TBUbicaciones.buscarSuscriptor === '') {
            WarningMessage('Debe ingresar el número de suscriptor.');
            TBUbicaciones.setLoading(false);
            return;
        }
        if (TBUbicaciones.buscarSuscriptor.length < 4) {
            WarningMessage('Debe ingresar un número de suscriptor con al menos 4 caracteres.');
            TBUbicaciones.setLoading(false);
            return;
        }

        TBUbicaciones.setLoading(true);

        const params = {
            suscriptor: TBUbicaciones.buscarSuscriptor
        }

        //console.log(JSON.stringify(data));
        const existe = await existeSuscriptor(params);
        if (existe === 99) {
            TBUbicaciones.setLoading(false);
            ErrorMessage('Oops...', 'No se pudo conectar con el servidor. Por favor, inténtelo más tarde.');
            return;
        }
        if (existe) {
            const data = {
                suscriptor: TBUbicaciones.buscarSuscriptor
            };

            const success = await obtenerCliente(data);
            if (success === 99) {
                TBUbicaciones.setLoading(false);
                ErrorMessage('Oops...', 'No se pudo conectar con el servidor. Por favor, inténtelo más tarde.');
                return;
            }

            if (success.estado) {
                const datos = success.data;
                //alert(JSON.stringify(datos.ip));
                TBUbicaciones.setMostrarEditar(false);
                TBUbicaciones.setMostrarGPS(false);
                TBUbicaciones.setEsNuevo(false);
                TBUbicaciones.setHabilitarLlamadas(false);
                TBUbicaciones.setMostarAgregar(true);
                TBUbicaciones.setMostrarGPS(false);
                TBUbicaciones.setEsNuevo(false);
                TBUbicaciones.setSuscriptor(datos.suscriptor);
                TBUbicaciones.setNombreCompleto(datos.nombreCompleto);
                TBUbicaciones.setTelefono1(datos.telefono1);
                TBUbicaciones.setTelefono2(datos.telefono2);
                TBUbicaciones.setTelefono3(datos.telefono3);
                TBUbicaciones.setPlan(datos.plan)
                TBUbicaciones.setNodo(datos.nodo)
                TBUbicaciones.setColilla(datos.colilla);
                TBUbicaciones.setTap(datos.tap);
                TBUbicaciones.setNap(datos.nap);
                TBUbicaciones.setMac(datos.mac);
                TBUbicaciones.setSn(datos.sn);
                TBUbicaciones.setIP(datos.ip);
                TBUbicaciones.setDireccion(datos.direccion);
                TBUbicaciones.setUbicacion(datos.ubicacion);
                TBUbicaciones.setLoading(false);
            }
        } else {
            TBUbicaciones.setLoading(false);

            Swal.fire({
                background: "#474747",
                color: "#FFFFFF",
                title: "¡Atención...",
                text: "El número de suscriptor ingresado no se encuentra registrado.\n\n¿Desea registrar este número de suscriptor?",
                icon: "info",
                showCancelButton: true,
                confirmButtonColor: "#28A745",
                cancelButtonColor: "#d33",
                confirmButtonText: "Registrar Suscriptor",
                cancelButtonText: "No"
            }).then((result) => {
                if (result.isConfirmed) {
                    //TBUbicaciones.setEditar(false);
                    TBUbicaciones.setMostarAgregar(false); // Esto habilita la vista para agregar un nuevo suscriptor
                    TBUbicaciones.setEsNuevo(true);
                    TBUbicaciones.setHabilitarLlamadas(true);
                    TBUbicaciones.setSuscriptor(TBUbicaciones.buscarSuscriptor)
                    TBUbicaciones.setNombreCompleto('');
                    TBUbicaciones.setTelefono1('');
                    TBUbicaciones.setTelefono2('');
                    TBUbicaciones.setTelefono3('');
                    TBUbicaciones.setPlan('');
                    TBUbicaciones.setNodo('');
                    TBUbicaciones.setColilla('');
                    TBUbicaciones.setTap('');
                    TBUbicaciones.setNap('');
                    TBUbicaciones.setMac('');
                    TBUbicaciones.setSn('');
                    TBUbicaciones.setIP('');
                    TBUbicaciones.setDireccion('');
                    TBUbicaciones.setUbicacion('');
                } else {
                    TBUbicaciones.setMostarAgregar(true);
                    TBUbicaciones.setBuscarSuscriptor('');
                    TBUbicaciones.setHabilitarLlamadas(true);
                    limpiarCampos(TBUbicaciones);
                }
            });

        }
    } catch (error) {
        console.error('Error en buscarSuscriptor:', error);
        TBUbicaciones.setLoading(false);
        ErrorMessage('Ocurrió un error al buscar el suscriptor.');
    }
};

