export const ListaCompleta = async (TBUbicaciones) => {
    try {
        if (!navigator.onLine) {
            TBUbicaciones.setLoading(false);
            WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
            return;
        }
        
        TBUbicaciones.setLoading(true);
        const Datos = await obtenerlistaCompleta();

        if (Datos === 99) {
            ErrorMessage('Oops!', 'No se pudo conectar con el servidor. Por favor, inténtelo más tarde.')
            TBUbicaciones.setMostrarListado(false);
            TBUbicaciones.setLoading(false);
            return;
        }
        //console.log(JSON.stringify(Datos)); 
        TBUbicaciones.setDatos(Datos)
        TBUbicaciones.setLoading(false);
        TBUbicaciones.setMostrarListado(true);

    } catch (error) {
        console.error('Error en buscarSuscriptor:', error);
        TBUbicaciones.setLoading(false);
        ErrorMessage('Ocurrió un error al buscar la lista de suscriptores.');
    }
}

/**
 * Método para limpiar los campos y restablecer el estado de la interfaz de usuario.
 * 
 * Este método restablece todos los valores de los campos relacionados con el suscriptor a su valor inicial (vacío) y restablece el estado de la interfaz de usuario para permitir una nueva operación o registro.
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene el estado y las funciones necesarias para gestionar los datos y la interfaz de usuario.
 * 
 * Este método realiza lo siguiente:
 * 1. Restablece los valores de los campos de entrada relacionados con el suscriptor a sus valores vacíos o predeterminados.
 * 2. Habilita y muestra los elementos de la interfaz de usuario necesarios para agregar, editar o eliminar un suscriptor.
 * 
 * @returns {void} No retorna ningún valor explícito. Modifica el estado de `TBUbicaciones` para restablecer la interfaz.
 */
export const limpiarCampos = (TBUbicaciones) => {
    TBUbicaciones.setMostrarEditar(true);
    TBUbicaciones.setMostarAgregar(true);
    TBUbicaciones.setEditar(true);
    TBUbicaciones.setMostrarGPS(true);
    TBUbicaciones.setModificarSuscritor(true);
    TBUbicaciones.setHabilitarGuardar(true);
    TBUbicaciones.setHabilitarEliminar(true);
    TBUbicaciones.setMostrarEditarSuscrip(true);
    TBUbicaciones.setHabilitarLlamadas(true);
    TBUbicaciones.setBuscarSuscriptor('');
    TBUbicaciones.setSuscriptor('');
    TBUbicaciones.setNombreCompleto('');
    TBUbicaciones.setTelefono1('');
    TBUbicaciones.setTelefono2('');
    TBUbicaciones.setTelefono3('');
    TBUbicaciones.setPlan('');
    TBUbicaciones.setNodo('');
    TBUbicaciones.setColilla('');
    TBUbicaciones.setTap('');
    TBUbicaciones.setNap('');
    TBUbicaciones.setMac('');
    TBUbicaciones.setSn('');
    TBUbicaciones.setIP('');
    TBUbicaciones.setDireccion('');
    TBUbicaciones.setUbicacion('');


};

/**
 * Método para habilitar y deshabilitar los controles de edición en la interfaz de usuario.
 * 
 * Este método desactiva los botones y campos que permiten editar o guardar información de un suscriptor,
 * dejando la interfaz en un estado donde no se puede modificar la información del suscriptor, 
 * solo se puede visualizar. 
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene el estado y las funciones necesarias para gestionar los datos y la interfaz de usuario.
 * 
 * Este método realiza lo siguiente:
 * 1. Deshabilita la capacidad de editar los datos del suscriptor.
 * 2. Deshabilita los botones de guardar y eliminar.
 * 3. Desactiva la opción de editar los datos del suscriptor desde la interfaz.
 * 
 * @returns {void} No retorna ningún valor explícito. Modifica el estado de `TBUbicaciones` para cambiar la interfaz de usuario.
 */
export const habilitarEditar = (TBUbicaciones) => {
    TBUbicaciones.setEditar(false);
    TBUbicaciones.setMostrarEditar(false);
    TBUbicaciones.setHabilitarGuardar(false);
    TBUbicaciones.setHabilitarEliminar(false);
    TBUbicaciones.setMostrarEditarSuscrip(false);
};

/**
 * Método para habilitar o deshabilitar la opción de modificar un suscriptor.
 * 
 * Este método desactiva la capacidad de modificar los datos de un suscriptor,
 * estableciendo un valor de `false` en el estado correspondiente a la modificación
 * del suscriptor. Usualmente, esto se hace para evitar que el suscriptor sea modificado
 * cuando no se desea realizar cambios sobre sus datos.
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene el estado y las funciones necesarias para gestionar los datos y la interfaz de usuario.
 * 
 * Este método realiza lo siguiente:
 * 1. Desactiva la opción de modificar un suscriptor en la interfaz de usuario.
 * 
 * @returns {void} No retorna ningún valor explícito. Modifica el estado de `TBUbicaciones` para cambiar la interfaz de usuario.
 */
export const habilitarSuscriptor = (TBUbicaciones) => {
    TBUbicaciones.setModificarSuscritor(false);
}

/**
 * Método para habilitar la opción de agregar un nuevo suscriptor.
 * 
 * Este método configura el estado para permitir agregar un nuevo suscriptor, mostrando
 * solo las opciones necesarias para agregarlo y deshabilitando las opciones que no son 
 * necesarias en este flujo. Además, establece valores predeterminados para ciertos campos 
 * en la interfaz de usuario relacionados con el proceso de agregar un suscriptor.
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene el estado y las funciones necesarias para gestionar los datos y la interfaz de usuario.
 * 
 * Este método realiza lo siguiente:
 * 1. Habilita la opción de modificar el suscriptor.
 * 2. Desactiva la edición de los datos del suscriptor.
 * 3. Oculta los elementos relacionados con la edición de un suscriptor.
 * 4. Desactiva la opción de guardar cambios en los datos del suscriptor.
 * 5. Muestra un campo con el número del suscriptor a agregar.
 * 
 * @returns {void} No retorna ningún valor explícito. Modifica el estado de `TBUbicaciones` para cambiar la interfaz de usuario.
 */
export const habilitarAgregar = (TBUbicaciones) => {
    TBUbicaciones.setModificarSuscritor(true);
    TBUbicaciones.setEditar(false);
    TBUbicaciones.setMostrarEditarSuscrip(false);
    TBUbicaciones.setMostrarGPS(false);
    TBUbicaciones.setSuscriptor(TBUbicaciones.buscarSuscriptor)
    TBUbicaciones.setHabilitarGuardar(false);
};

/**
 * Método para abrir la ubicación en Google Maps.
 * 
 * Este método recibe una ubicación en formato de texto (latitud, longitud) desde 
 * el objeto `TBUbicaciones`, valida que la ubicación sea válida (números de latitud y longitud),
 * y abre Google Maps en el navegador o aplicación del dispositivo con la ubicación proporcionada.
 * Si la ubicación no es válida o el formato es incorrecto, se muestran alertas correspondientes.
 * 
 * @param {Object} Linking - Objeto que permite interactuar con el sistema operativo para abrir enlaces, como URLs.
 * @param {Object} TBUbicaciones - Objeto que contiene el estado y los datos relacionados con la ubicación a mostrar.
 * 
 * @returns {void} No retorna ningún valor. Solo abre la URL de Google Maps si la ubicación es válida.
 */
export const AbrirMapa = (TBUbicaciones) => {
    if (!TBUbicaciones || !TBUbicaciones.ubicacion) {
        return alert('La ubicación no está disponible.');
    }

    const ubicacionTrimmed = TBUbicaciones.ubicacion.trim();

    if (ubicacionTrimmed) {
        const [latitud, longitud] = ubicacionTrimmed.split(',').map(coord => parseFloat(coord.trim()));

        if (!isNaN(latitud) && !isNaN(longitud)) {
            const geoUrl = `https://www.google.com/maps?q=${latitud},${longitud}`;

            window.open(geoUrl, '_blank'); // Abre en una nueva pestaña o la aplicación de mapas predeterminada
        } else {
            ErrorMessage('Ubicación no válida');
        }
    } else {
        InfoMessage('Debes ingresar la latitud y longitud');
    }
};


/**
 * Registra un cliente en el sistema.
 * 
 * Este método valida los campos requeridos para registrar un nuevo cliente, verifica si el número 
 * de suscriptor ya existe en el sistema, y si es válido, guarda los datos del cliente en la base de datos.
 * Si todo el proceso es exitoso, limpia los campos del formulario; de lo contrario, muestra alertas de advertencia.
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene los datos del cliente a registrar.
 * 
 * @returns {void} No retorna ningún valor. Solo realiza el proceso de validación, registro y manejo de alertas.
 */
export const RegistarCliente = async (TBUbicaciones) => {

    if (TBUbicaciones.suscriptor == '') {
        WarningMessage('No se ha seleccionado un suscriptor');
        return;
    }
    if (TBUbicaciones.nombreCompleto === '') {
        WarningMessage('No se ha ingresado el nombre del cliente');
        return;
    }
    if (TBUbicaciones.direccion === '') {
        WarningMessage('No se ha ingresado la direccion del cliente');
        return;
    }
    if (TBUbicaciones.ubicacion === '') {
        WarningMessage('No se ha ingresado la ubicación del cliente');
        return;
    }

    const ubicacionRegex = /^-?\d+(\.\d+)?,\s*-?\d+(\.\d+)?$/;
    if (!ubicacionRegex.test(TBUbicaciones.ubicacion)) {
        WarningMessage('La ubicación debe contener latitud y longitud en el formato "00.000000,-00.000000".');
        return;
    }
    if (!navigator.onLine) {
        TBUbicaciones.setLoading(false);
        WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
        return;
    }
    const params = {
        suscriptor: TBUbicaciones.suscriptor
    }

    //console.log(JSON.stringify(data));
    const existe = await existeSuscriptor(params);
    //alert(JSON.stringify(existe));
    if (existe) {
        WarningMessage('El número de suscriptor ya existe');
        limpiarCampos(TBUbicaciones)
        return;
    } else {
        if (!navigator.onLine) {
            TBUbicaciones.setLoading(false);
            WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
            return;
        }
        const data = {
            suscriptor: TBUbicaciones.suscriptor,
            nombreCompleto: TBUbicaciones.nombreCompleto,
            telefono1: TBUbicaciones.telefono1,
            telefono2: TBUbicaciones.telefono2,
            telefono3: TBUbicaciones.telefono3,
            plan: TBUbicaciones.plan,
            nodo: TBUbicaciones.nodo,
            colilla: TBUbicaciones.colilla,
            tap: TBUbicaciones.tap,
            nap: TBUbicaciones.nap,
            mac: TBUbicaciones.mac,
            sn: TBUbicaciones.sn,
            ip: TBUbicaciones.ip,
            direccion: TBUbicaciones.direccion,
            ubicacion: TBUbicaciones.ubicacion
        }
        const success = await guardarSuscriptor(data);
        //alert(success)
        if (success.estado) {
            SuccessMessage('Exitoso¡', 'Cliente registrado');
            limpiarCampos(TBUbicaciones)
        } else {
            ErrorMessage(success.message);
        }
    }
};

export const cantClientes = async (TBUbicaciones) => {

    const cantidad = await cantidadClientes()
    TBUbicaciones.setCanti(cantidad)
};

/**
 * Actualiza los datos de un cliente existente.
 * 
 * Este método valida los campos requeridos para actualizar un cliente, y si todo está correcto, 
 * envía los nuevos datos a la base de datos. Si la actualización es exitosa, limpia los campos del formulario;
 * de lo contrario, muestra una alerta con el error.
 * 
 * @param {Object} TBUbicaciones - Objeto que contiene los datos del cliente a actualizar.
 * 
 * @returns {void} No retorna ningún valor. Solo realiza el proceso de validación, actualización y manejo de alertas.
 */
export const ActualizarCliente = async (TBUbicaciones) => {

    if (TBUbicaciones.suscriptor == '') {
        WarningMessage('No se ha seleccionado un suscriptor');
        return;
    }
    if (TBUbicaciones.nombreCompleto === '') {
        WarningMessage('No se ha ingresado el nombre del cliente');
        return;
    }
    if (TBUbicaciones.direccion === '') {
        WarningMessage('No se ha ingresado la direccion del cliente');
        return;
    }
    if (TBUbicaciones.ubicacion === '') {
        WarningMessage('No se ha ingresado la ubicación del cliente');
        return;
    }

    const ubicacionRegex = /^-?\d+(\.\d+)?,\s*-?\d+(\.\d+)?$/;
    if (!ubicacionRegex.test(TBUbicaciones.ubicacion)) {
        WarningMessage('La ubicación debe contener latitud y longitud en el formato "00.000000,-00.000000"');
        return;
    }
    const data = {
        suscriptor: TBUbicaciones.suscriptor,
        nuevoSuscriptor: TBUbicaciones.suscriptor,
        nombreCompleto: TBUbicaciones.nombreCompleto,
        telefono1: TBUbicaciones.telefono1,
        telefono2: TBUbicaciones.telefono2,
        telefono3: TBUbicaciones.telefono3,
        plan: TBUbicaciones.plan,
        nodo: TBUbicaciones.nodo,
        colilla: TBUbicaciones.colilla,
        tap: TBUbicaciones.tap,
        nap: TBUbicaciones.nap,
        mac: TBUbicaciones.mac,
        sn: TBUbicaciones.sn,
        ip: TBUbicaciones.ip,
        direccion: TBUbicaciones.direccion,
        ubicacion: TBUbicaciones.ubicacion
    }
    if (!navigator.onLine) {
        TBUbicaciones.setLoading(false);
        WarningMessage('Sin conexión a internet', 'Por favor, verifique su conexión a internet e inténtelo nuevamente.');
        return;
    }
    const success = await actualizarSuscriptor(data);

    //console.log(success);
    if (success.estado) {
        SuccessMessage(success.message)
        limpiarCampos(TBUbicaciones)
    }
    else {
        ErrorMessage(success.message)
    }
};

