import EndPoint from '../../CapaEP/EP/EndPoint';
import { ErrorMessage } from '../../CapaUtilidades/Utils/FuncionesDeReutilizacion';

var url = '';


/**
 * Obtiene los datos de un cliente desde el API.
 * 
 * Esta función realiza una solicitud POST a un servidor utilizando la URL del endpoint especificado, 
 * pasando los datos proporcionados en el cuerpo de la solicitud. Si la solicitud es exitosa y el 
 * estado es positivo, devuelve los datos del cliente. En caso contrario, retorna el estado del API.
 * 
 * @param {Object} data - Objeto que contiene los datos necesarios para obtener el cliente.
 * 
 * @returns {Object | boolean | number} Devuelve los datos del cliente si la respuesta es exitosa,
 *          el estado de la respuesta si no es exitoso, o `0` en caso de error.
 */
export async function obtenerlistaCompleta() {
    const url = EndPoint.EpTodosLosClientes; // URL del endpoint
    //alert(url);
    if (!url) {
        ErrorMessage('Error', 'URL no especificada.');
        return null;
    }
    
    try {
        
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
        });

         

        const responseData = await response.json();
         //alert(responseData);

        if (responseData.estado) {
            return responseData.data;
        } else {
            
            return responseData.estado;
        }
    } catch (error) {
        console.error('Error en la solicitud:', error.message);
        console.log('Stack Trace:', error.stack);
        return 99;
    }
}



/**
 * Obtiene los datos de un cliente desde el API.
 * 
 * Esta función realiza una solicitud POST a un servidor utilizando la URL del endpoint especificado, 
 * pasando los datos proporcionados en el cuerpo de la solicitud. Si la solicitud es exitosa y el 
 * estado es positivo, devuelve los datos del cliente. En caso contrario, retorna el estado del API.
 * 
 * @param {Object} data - Objeto que contiene los datos necesarios para obtener el cliente.
 * 
 * @returns {Object | boolean | number} Devuelve los datos del cliente si la respuesta es exitosa,
 *          el estado de la respuesta si no es exitoso, o `0` en caso de error.
 */
export async function obtenerCliente(data) {
    const url = EndPoint.EpObtenerCliente; // Asegúrate de que esta URL sea válida.

    
    // Verifica si la URL y los datos están presentes
    if (!url || !data) {
        ErrorMessage('Error', 'URL o datos no especificados.');
        return null;
    }

    try {
        // Realiza la solicitud POST
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });

        // Verifica si la respuesta fue exitosa (código HTTP 200-299)
        if (!response.ok) {
            throw new Error(`Error en la solicitud: ${response.statusText}`);
        }

        // Convierte la respuesta en JSON
        const responseData = await response.json();
       
        console.log(JSON.stringify(responseData));
        return responseData;
        

    } catch (error) {
        // Manejo de errores: muestra el error y la traza de la pila en la consola
        console.error('Error en la solicitud:', error);
        console.log('Stack Trace:', error.stack);

        // Devuelve un valor de error, por ejemplo, 99 (puedes usar otro código si prefieres)
        return 99;
    }
}


/**
 * Verifica si un suscriptor ya existe en el sistema.
 * 
 * Esta función realiza una solicitud POST al servidor utilizando la URL del endpoint especificado,
 * pasando los datos proporcionados en el cuerpo de la solicitud. Si la solicitud es exitosa, retorna
 * un valor que indica si el suscriptor ya existe. En caso de error, retorna `false`.
 * 
 * @param {Object} data - Objeto que contiene los datos necesarios para verificar la existencia del suscriptor.
 * 
 * @returns {boolean} Devuelve `true` si el suscriptor ya existe, o `false` si no existe o en caso de error.
 */
export async function existeSuscriptor(data) {

    url = EndPoint.EpExisteCliente

    if (!url || !data) {
        alert('Error', 'URL o datos no especificados.', 'danger');
        return null;
    }

    try {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });



        const responseData = await response.json();

        //
        if (responseData.estado) {
            //alert(JSON.stringify(responseData.existeColaborador));
            return responseData.existeColaborador;
        } else {
            return responseData.existeColaborador;
        }



    } catch (error) {
        console.error('Error en la solicitud:', error);
        console.log('Stack Trace:', error.stack);
        // console.error('Error de inicio de sesión:', error);
        return 99;
    }

}

/**
 * Registra un nuevo suscriptor en el sistema.
 * 
 * Esta función realiza una solicitud POST al servidor para guardar los datos del nuevo suscriptor.
 * Si la solicitud es exitosa, retorna el estado de la operación (si se registró correctamente).
 * En caso de error, muestra un mensaje de alerta con el error recibido del servidor o un error HTTP.
 * 
 * @param {Object} data - Objeto que contiene los datos del suscriptor a registrar.
 * 
 * @returns {boolean} Devuelve `true` si el suscriptor fue registrado correctamente, 
 * o `false` si hubo un error o la respuesta del servidor indica un fallo.
 */
export async function guardarSuscriptor(data) {
    const url = EndPoint.EpRegistrarCliente
    //Alert.alert(url);
    if (!url || !data) {
        alert('Error', 'URL o datos no especificados.', 'danger');
        return null;
    }
    try {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });

        // if (!response.ok) {
        //     const errorResponseData = await response.json();
        //     console.error(`Error en la solicitud: ${JSON.stringify(errorResponseData)}`);
        //     alert('Error', errorResponseData.message || `HTTP error! status: ${response.status}`, 'danger');
        //     return false;
        // }

        const responseData = await response.json();

        
        return responseData;


    } catch (error) {
        console.error('Error en la solicitud:', error);
        console.log('Stack Trace:', error.stack);
        // console.error('Error de inicio de sesión:', error);
        return {estado: false, message: "Error en la solicitud"};
    }
}

/**
 * Actualiza los datos de un suscriptor existente en el sistema.
 * 
 * Esta función realiza una solicitud POST al servidor para actualizar los datos de un suscriptor específico.
 * Si la solicitud es exitosa, retorna la respuesta del servidor (que puede incluir el estado de la operación o algún mensaje).
 * En caso de error, muestra un mensaje de alerta con el error recibido o un error de red.
 * 
 * @param {Object} data - Objeto que contiene los datos del suscriptor a actualizar.
 * 
 * @returns {Object} Devuelve la respuesta del servidor, que puede incluir el estado de la operación, 
 * o `null` si hubo un error o la solicitud no fue realizada correctamente.
 */
export async function actualizarSuscriptor(data) {
    const url = EndPoint.EpEditarCliente
    //Alert.alert(url);
    if (!url || !data) {
        alert('Error', 'URL o datos no especificados.', 'danger');
        return null;
    }
    try {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });

        

        const responseData = await response.json();

        
        return responseData;


    } catch (error) {
        console.error('Error en la solicitud:', error);
        console.log('Stack Trace:', error.stack);
        // console.error('Error de inicio de sesión:', error);
        return {estado: false, message: "error en la solicitud"};
    }
}


export async function cantidadClientes( ) {

    url = EndPoint.EPCantidadClientes

    if (!url ) {
        alert('Error', 'URL o datos no especificados.', 'danger');
        return null;
    }

    try {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
        });



        const responseData = await response.json();

        //
        if (responseData.estado) {
            //alert(JSON.stringify(responseData.existeColaborador));
            return responseData.cantidad;
        } else {
            return 0;
        }



    } catch (error) {
        console.error('Error en la solicitud:', error);
        console.log('Stack Trace:', error.stack);
        // console.error('Error de inicio de sesión:', error);
        return '-';
    }

}